import { Box, Button, Typography } from "@mui/material";
import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import Images from "../assets";

const EmailVerification: React.FC = () => {
  const navigate = useNavigate();
  const [success, setSuccess] = useState<string | null>("");
  const [info, setInfo] = useState<string | null>("");

  useEffect(() => {
    const getSuccessFromURL = async () => {
      const urlParams = new URLSearchParams(window.location.search);
      const receivedSuccess = urlParams.get("success");
      const infoParam = urlParams.get("info");
      setSuccess(receivedSuccess);
      setInfo(infoParam);
    };
    getSuccessFromURL();
  }, []);

  return (
    <div>
      <Box sx={{ my: 10 }}>
        {success === "true" ? (
          <Box
            sx={{
              display: "flex",
              flexDirection: "column",
              justifyContent: "center",
              alignItems: "center",
            }}
          >
            <img
              src={Images.emailVerified}
              alt="verification"
              style={{ height: "180px" }}
            />
            <Typography variant="h5" fontWeight="bold" fontSize="32px">
              Verified email!
            </Typography>
            <Typography>You have successfully verified your email</Typography>
            <Button
              sx={{ mt: 3 }}
              onClick={() => navigate("/")}
            >
              Continue
            </Button>
          </Box>
        ) : (
          <Box
            sx={{
              display: "flex",
              flexDirection: "column",
              justifyContent: "center",
              alignItems: "center",
            }}
          >
            <img
              src={Images.emailFailed}
              alt="verification"
              style={{ height: "180px" }}
            />
            {info === null ? (
              <>
                <Typography variant="h5" fontWeight="bold" fontSize="32px">
                  Verification Failed
                </Typography>
                <Typography>
                  Your email verification is not successful
                </Typography>
              </>
            ) : (
              <>
                <Typography variant="h5" fontWeight="bold" fontSize="32px">
                  Already Verified
                </Typography>
                <Typography>It seems you already verified your mail</Typography>
              </>
            )}
            <Button
              sx={{ mt: 3 }}
              onClick={() => navigate("/")}
            >
              Continue
            </Button>
          </Box>
        )}
      </Box>
    </div>
  );
};

export default EmailVerification;
